import React, {Component} from 'react';
import {Breadcrumb, BreadcrumbItem,
    Button,  Label , Col, Row,
} from 'reactstrap';
import {Link} from 'react-router-dom';
import {Control,LocalForm,Errors, actions, Form} from 'react-redux-form';

// khai báo các lỗi và cài đặt các trường hợp xảy ra lỗi cho form
const required = (val) => val && val.length;
const maxLength = (len) => (val) => !val || val.length <= len;
const minLength = (len) => (val) => val && val.length >= len;
const isNumber = (val) => !isNaN(Number(val));
const validEmail = (val) => /^[A-Z0-9._%+-]+@[A-Z0-9._%+-]+\.[A-Z]{2,4}$/i.test(val);

class Contact extends Component {

    constructor(props) {
        super(props);

        this.handleSubmit = this.handleSubmit.bind(this);    
    }

    // Buton Submit hiện lên bảng cảnh báo giá trị values trong form vừa nhập
    handleSubmit(values) {
        console.log("Current State is: " + JSON.stringify(values));
        alert("Current State is: " + JSON.stringify(values));
        this.props.resetFeedbackForm();
    }
  
    render() {    
        return(
            // info
            <div className="container">
                <div className='row'>
                    <Breadcrumb>
                        <BreadcrumbItem><Link to='/home'>Home</Link></BreadcrumbItem>
                        <BreadcrumbItem active>Contact Us</BreadcrumbItem>
                    </Breadcrumb>
                    <div className='col-12'>
                        <h3>Contact Us</h3>
                        <hr />
                    </div>
                </div>
                <div className="row row-content">
                    <div className="col-12">
                    <h3>Location Information</h3>
                    </div>
                    <div className="col-12 col-sm-4 offset-sm-1">
                            <h5>Our Address</h5>
                            <address>
                            121, Clear Water Bay Road<br />
                            Clear Water Bay, Kowloon<br />
                            HONG KONG<br />
                            <i className="fa fa-phone"></i>: +852 1234 5678<br />
                            <i className="fa fa-fax"></i>: +852 8765 4321<br />
                            <i className="fa fa-envelope"></i>: <a href="mailto:confusion@food.net">confusion@food.net</a>
                            </address>
                    </div>
                    <div className="col-12 col-sm-6 offset-sm-1">
                        <h5>Map of our Location</h5>
                    </div>
                    <div className="col-12 col-sm-11 offset-sm-1">
                        <div className="btn-group" role="group">
                            <a role="button" className="btn btn-primary" href="tel:+85212345678"><i className="fa fa-phone"></i> Call</a>
                            <a role="button" className="btn btn-info"><i className="fa fa-skype"></i> Skype</a>
                            <a role="button" className="btn btn-success" href="mailto:confusion@food.net"><i className="fa fa-envelope-o"></i> Email</a>
                        </div>
                    </div>
                </div>
                <div className='row row-content'>
                    <div className='col-12'>
                        <h3>Send us your Feedback</h3>
                    </div>
                    {/* form */}
                    <div className='col-12 col-sm-9'>
                        <Form model='feedback' onSubmit={(values) => this.handleSubmit(values)}>
                            {/* firstName */}
                            <Row className='form-group'>
                                <Label htmlFor='firstname' md = {2}>First Name</Label>
                                <Col md={10}>
                                    <Control.text model='.firstname' id='firstname' name='firstname'
                                        placeholder="First Name"
                                        className='form-control'
                                        validators={{
                                            required,
                                            minLength : minLength(3),
                                            maxLength : maxLength(15)
                                        }} />
                                    <Errors className='text-danger' model='.firstname' show='touched'
                                        messages={{
                                            required : 'Required',
                                            minLength : 'Must be greater than 2 character ',
                                            maxLength : 'Must be 15 numbers or less'
                                        }} />
                                </Col>
                            </Row>
                            {/* last Name */}
                            <Row className='form-group'>
                                <Label htmlFor='lastname' md={2}>Last Name</Label>
                                <Col md={10}>
                                    <Control.text model='.lastname' id='lastname' name='lastname'
                                        placeholder='Last Name'
                                        className='form-control'
                                        validators={{
                                            required,
                                            minLength : minLength(3),
                                            maxLength : maxLength(15)
                                        }} />
                                    <Errors className='text-danger' show='touched' model='.lastname'
                                        messages={{
                                            required : 'Required',
                                            minLength : 'Must be greater than 2 character',
                                            maxLength : 'Must be 15 numbers or less'
                                        }} />
                                </Col>
                            </Row>
                            {/* Tel Num */}
                            <Row className='form-group'>
                                <Label htmlFor='telnum' md={2}>Tel Num</Label>
                                <Col md={10}>
                                    <Control.text model='.telnum' id='telnum' name='telnum'
                                        placeholder='Tel Num'
                                        className='form-control' 
                                        validators={{
                                            required,
                                            isNumber
                                        }}/>
                                    <Errors className='text-danger' show='touched' model='.telnum'
                                        messages={{
                                            required : 'Required',
                                            isNumber : 'Must be a number'
                                        }} />
                                </Col>
                            </Row>
                            {/* Email */}
                            <Row className='form-group'>
                                <Label htmlFor='email' md={2}>Email</Label>
                                <Col md={10}>
                                    <Control.text model='.email' id='email' name='email'
                                        placeholder='Email'
                                        className='form-control' 
                                        validators={{
                                            required,
                                            validEmail
                                        }}
                                    />
                                    <Errors className='text-danger' show='touched' model='.email'
                                        messages={{
                                            required : 'Required',
                                            validEmail : 'Invalid Email Adress'
                                        }} 
                                    />
                                </Col>
                            </Row>
                            {/* Checkbox + select ContacType */}
                            <Row className='form-group'>
                                <Col md={{size : 6,offset:2}}>
                                    <div className='form-check'>
                                        <Label check>
                                            <Control.checkbox model='.agree' name='agree'
                                                className='form-check-input' />{' '}
                                                <strong>May we contact us?</strong>
                                        </Label>
                                    </div>
                                </Col>
                                <Col md={{size:3,offset:1}}>
                                    <Control.select model='.contactType' name='contactType'
                                        className='form-control'>
                                            <option>Tel.</option>
                                            <option>Email</option>
                                        </Control.select>
                                </Col>
                            </Row>
                            {/* message */}
                            <Row className='form-group'>
                                <Label htmlFor="message" md={2}>Your Feedback</Label>
                                <Col md={10}>
                                    <Control.textarea model=".message" id="message" name="message"
                                        rows="12"
                                        className='form-control' />
                                </Col>
                            </Row>
                            {/* Button submit Form */}
                            <Row className='form-group'>
                                <Col md={{size : 10,offset:2}}>
                                    <Button type='submit' color='primary'>
                                        Send Feedback
                                    </Button>
                                </Col>
                            </Row>
                        </Form>
                    </div>
                </div>
            </div>
        );
    }
}

export default Contact;